body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    height: 100vh;
    width: 100vw;
    overflow:hidden;
}

.error{
  color: #f05305;
}

.popup-img{
  width:20vw;
  object-fit: contain;
  text-align:center;
  margin:0 auto;
}

.marker{
  height:24px;
  width:24px;
  transform: translate(-50%,-100% );
}
.marker.red path{
fill: red;
}

.marker.yellow path{
  fill:goldenrod;
  }
  

.popup{
  margin:0;
}
.popup > h3{
  font-size:1.4rem;
  margin:0.2rem;
}
.popup > p{
  font-size: 1rem;
}
.popup > small{
  font-size: 0.8rem;
}
.entry-form{
  width:30vw;
  padding:0.5rem;
}
.entry-form textarea{
  resize: none;
}

.entry-form label,.entry-form textarea, .entry-form input,.entry-form button{
  font-size:1rem;
  margin:0.5rem 0;
  display:block;
  width:100%;
}


